<template>
  <a-form :form="form" @submit="handleSubmit">
    <a-alert :message="`设计ID：`+id" type="info" banner />
    <a-form-item label="标题" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
      <a-input
        v-decorator="[
          'title',
          {rules: [{ required: true, message: '标题不能为空！' }]}
        ]"
      />
    </a-form-item>
    <a-form-item label="技术简介" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
      <a-input
        v-decorator="[
          'subtitle',
          {rules: [{ required: true, message: '技术简介不能为空！' }]}
        ]"
      />
    </a-form-item>
    <a-form-item label="价格" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
      <a-input
        v-decorator="[
          'price',
          {rules: [{ required: true, message: '价格不能为空！' }]}
        ]"
      />
    </a-form-item>
    <a-form-item label="内容" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
      <a-textarea
        :autosize="{ minRows: 5, maxRows: 6 }"
        v-decorator="[
          'content',
          {rules: [{ required: true, message: '内容不能为空！' }]}
        ]"
      />
    </a-form-item>
    <a-form-item :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" label="是否有文档">
      <!-- <a-radio-group
          v-decorator="[
          'document',
          {rules: [{ required: true, message: '请选择是否有文档！' }]}
        ]"
        >
          <a-radio value="true">是</a-radio>
          <a-radio value="false">否</a-radio>
      </a-radio-group>-->

      <!-- <a-checkbox
        v-decorator="[
          'document',
          {rules: [{ required: true, message: '请选择是否有文档！', }]}
        ]"
      >是否有文档</a-checkbox>-->

      <a-switch
        checkedChildren="是"
        unCheckedChildren="否"
        v-decorator="['document', { valuePropName: 'checked',initialValue:false }]"
      />
    </a-form-item>
    <a-form-item label="设计语言" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
      <a-select
        showSearch
        v-decorator="[
          'language',
          {rules: [{ required: true, message: '请选择设计语言！' }]}
        ]"
        placeholder="请选择设计语言"
        @change="handleSelectChange"
      >
        <a-select-option v-for="(item,index) in languages" :key="index" :value="item">{{item}}</a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item label="类型" :label-col="{ span:5}" :wrapper-col="{ span: 12 }">
      <a-select
        v-decorator="[
          'type',
          {rules: [{ required: true, message: '请选择设计类型！' }]}
        ]"
        placeholder="请选择设计类型"
        @change="handleSelectChange"
      >
        <a-select-option value="app">app</a-select-option>
        <a-select-option value="desktop">desktop</a-select-option>
        <a-select-option value="web">web</a-select-option>
        <a-select-option value="iot">iot</a-select-option>
        <a-select-option value="discount">discount</a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item label="标签" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
      <a-select
        mode="tags"
        :tokenSeparators="[',']"
        v-decorator="[
          'tags',
          {rules: [{ required: true, message: '请选择标签！' }]}
        ]"
        placeholder="请选择标签"
        @change="handleSelectChange"
      >
        <a-select-option v-for="(item,index) in languages" :key="index" :value="item">{{item}}</a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item label="图片" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
      <a-textarea
        placeholder="一行一个图片链接"
        :autosize="{ minRows: 5, maxRows: 6 }"
        v-decorator="[
          'images',
          {rules: [{ required: true, message: '图片不能为空！' }]}
        ]"
      />
    </a-form-item>

    <a-form-item label="视频" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
      <a-textarea
        placeholder="一行一个视频链接"
        :autosize="{ minRows: 5, maxRows: 6 }"
        v-decorator="[
          'videos',
          {rules: [{ required: false, message: '视频不能为空！' }]}
        ]"
      />
    </a-form-item>
    <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
      <a-button type="primary" html-type="submit">发布</a-button>
    </a-form-item>
  </a-form>
</template>

<script>
import { ObjectID } from "bson";
export default {
  data() {
    return {
      formLayout: "horizontal",
      form: this.$form.createForm(this),
      id: new ObjectID().toString(),
      languages: [
        "c#",
        "java",
        "wpf",
        "asp.net",
        "winform",
        "swing",
        "javafx",
        "electron",
        "小程序",
        "vue",
        "react",
        "react native",
        "flutter",
        "android",
        "arduino",
        "c",
        "stm32",
        "树莓派",
        "sqlite",
        "sqlserver",
        "mysql",
        "mongodb",
        "jsp",
        "php",
        "spring boot"
      ]
    };
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          values.id = this.id;

          values.images = values.images.split("\n");

  

          values.videos =
            typeof values.videos === "undefined"
              ? (values.videos = [])
              : values.videos.split("\n");
          values.price = parseInt(values.price);
      
          //console.log(values);

          this.axios
            .post("design", values)
            .then(response => {
              console.log(response.data);
              this.$message.success("发布设计成功!");
            })
            .catch(response => {
              console.log(response);
            });
        }
      });

      // const id = new ObjectID().toString();
      //console.log(id.toString());
    },
    handleSelectChange(value) {
      //   console.log(value);
      //   this.form.setFieldsValue({
      //     note: `Hi, ${value === "male" ? "man" : "lady"}!`
      //   });
    },
    mounted() {}
  }
};
</script>

